  .margins {
    margin-left:40px;
    margin-right:40px;
    
  }
}
@media only screen and (min-width: 800px) {
  .margins {
    margin-left:10px;
    margin-right:10px;
    
  }
}
.mart{
  margin-top:100px;
}
.img{
float:left;
width:100%;
margin-bottom:20px;
}
@media only screen and (min-width: 800px) {
 .img{
   width:60%;
   margin-top:100px
 }
    
  }

.center{
  font-family: monospace;
  text-align:center;
  margin-top:100px;
  
}



.sticky{position:sticky;top:0;background:white;}
.nav{
  display:inline;
  margin-left: 10px;
  color:black;
  
}
.nav:hover{background:grey;color:white;}
#links{

 
  list-style-type: none;
 
  padding: 0;
  width: 100%;
  background-color:white;

}
li{
  font-size: 150%;
  display: block;
  color: #000;
  padding: 8px 16px;
 
  color:black;
  border:1px solid black;
 

}
li:hover{
  background: grey;
  color:white;

}
#cimmermann{
  position:relative
}
.img-top{
  position: absolute;
  top:2%;
left:10%;
text-shadow: 1px 0 0 white, 0 -1px 0 white, 0 1px 0 white, -1px 0 0 white;
}
.img-bottom{
  position: absolute;
  bottom:2%;
left:10%;
color:white;
text-shadow: 4px 0 0 #000, 0 -1px 0 #000, 0 1px 0 #000, -1px 0 0 #000;
}
.dropp{
  position: relative;
  display: inline;
}
.articles {
  right:0;
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 300px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
}
.dropp:hover .articles {
  display: block;
}
.hov:hover{
 font-size:110%;
 color:black;
}
